import React, { FC } from 'react';
import {
  ColumnsProps,
  cpfFormatter,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, ResponsavelMenorAprendiz } from '../../../../models/dtos/mfp/responsavelMenorAprendiz';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const ResponsavelMenorAprendizSearchView: FC<SearchViewProps<ResponsavelMenorAprendiz>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Nome do Responsável',
    },
    {
      dataField: 'cpf',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cpfFormatter,
      sort: true,
      text: 'CPF',
    },
  ];

  return (
    <WindowComponent title="Manutenção de Responsável pelo Aprendiz">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ResponsavelMenorAprendizEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ResponsavelMenorAprendizSearchView;
